import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

const state = {
    uniacid :'',
    id:'',
    hostUrl:'',
    SalesRankData:''
  }
  
  const getters = {
    // editSignRow:state => state.hostlist,
  
  }
  
  const mutations = {
    Vuniacid(Vuniacid, data) {
      state.uniacid = data
      // console.log(data)
    },
    Vid(Vid, data) {
      state.id = data
      // console.log(data)
    },
    VhostUrl(VhostUrl, data) {
      state.hostUrl = data
      // console.log(data)
    },
    VSalesRankData(VSalesRankData,data){
      state.SalesRankData = data
    }

  }
  
  const actions = {
  
  }
  
  export default new Vuex.Store({
    state,
    getters,
    mutations,
    actions
  })
  


