<template>
  <div id="app">
    <router-view/>
    <!-- <p class="longbing">longbingkeji</p> -->
    <cmpBottom></cmpBottom>
    <p class="bottom"></p>
    <div class="return_index" @click="returnIndex">
      <figure class="img111">
        <img src="./assets/img/index.png" alt="" style="width:100%;">
      </figure>
       <!-- <i class="iconfont icon-index"></i> -->
    </div>
  </div>
</template>

<script>
import cmpBottom from "./components/common/index_bottom.vue";
import Common from "./assets/js/common.js";
let Mycommon = new Common();
export default {
  components: { cmpBottom },
  name: "App",
  computed: {
    bankName() {
      return this.$store.state;
    }
  },
  created() {
    this.$store.commit("VhostUrl", window.location.host);
    let id = Mycommon.getUrlParam(window.location.href, "id");
    this.$store.commit("Vid", id);
    let uniacid = Mycommon.getUrlParam(window.location.href, "uniacid");
    this.$store.commit("Vuniacid", uniacid);
    // let bottom_url = Mycommon.getUrlParam(window.location.href,'uniacid')
    // this.$store.commit('Vuniacid',uniacid)
    // console.log(uniacid)
  },
  methods: {
    returnIndex() {
      wx.miniProgram.reLaunch({
        url: "/longbing_card/pages/index/index"
      });
    }
  }
};
</script>

<style>
#app {
  font-family: "Microsoft Yahei";

  width: 100vw;
  font-size: 24px;
}
ul,
li {
  list-style: none;
}
.bottom {
  width: 100vw;
  height: 100px;
}
.longbing {
  height: 200px;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.return_index {
  width: 80px;
  height: 80px;
  background: rgba(0,0,0,0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 15vw;
  right: 0;
  z-index: 9;
  border-radius: 20px;
}
.icon-index {
  font-size: 50px;
  color: #848484;
}
.img111 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
}
</style>



