import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import SalesRank from '@/components/SalesRank'
import C_list from '@/components/C_list'
import AIRank from '@/components/AIRank'
import AIRankDetails from '@/components/AIRankDetails'
import C_details from '@/components/C_details'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      name: 'index',
      component: index
    },
    {
      path:'/SalesRank',
      name: 'SalesRank',
      component: SalesRank
    },
    {
      path:'/C_list',
      name: 'C_list',
      component: C_list
    },
    {
      path:'/AIRank',
      name: 'AIRank',
      component: AIRank
    },
    {
      path:'/AIRankDetails',
      name: 'AIRankDetails',
      component: AIRankDetails
    },
    {
      path:'/C_details',
      name: 'C_details',
      component: C_details
    },
  ]
})



